.jobFilterWrapper,
.filterPostWrapper {
  width: 100%;
  background-color: white;
  box-shadow: 0 0 8px 4px rgba(0, 0, 0, 0.048);
  padding: 0.3rem;
  margin: 0.3rem 0;
  border-radius: 0.3rem;
  grid-column: 1/2;
  display: flex;
  flex-direction: column;
  max-width: 300px;
}

.jobFilterTag,
.filerPostTag {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.3rem;
}
