.FeedPostContent {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-create-post {
  background-color: var(--mainLightBlueColor);
  padding: 0.175rem 0.55rem;
  margin: 0.5rem;
  border-radius: 0.25rem;
  border: 1px solid transparent;
  color: white;
  font-weight: bolder;
  font-size: 1rem;
  line-height: 1.5rem;
  transition: all 0.2s ease-in-out;
}

.button-create-post:hover {
  background-color: var(--mainLightBlueColorHover);
}