.avatarWrapper {
  position: relative;
  display: flex;
  justify-content: center;
}

.avatar {
  border: solid 3px var(--mainLightBlueColor);
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  box-shadow: 0px 0px 1px 1px var(--mainLightBlueColor);
}

.avatar.small {
  border: solid 2px var(--mainLightBlueColor);
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}

.avatar.huge {
  width: 8rem;
  height: 8rem;
}
.avatar.big {
  width: 5rem;
  height: 5rem;
}

.avatar.tiny {
  border: solid 1px var(--mainLightBlueColor);
  width: 2rem;
  height: 2rem;
}

.avatar img {
  margin: 0 auto;
  min-width: 100%;
  max-height: 100%;
}
